@use './assets/mixins' as *;
@use './assets/vars' as *;
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: var(--header-h);
  z-index: 40;
  background: #080700;
  transition: all 1500ms cubic-bezier(0.06, 0.65, 0.2, 1);
  transform: translateY(0%);

  &.header-hidden {
    transform: translateY(-100%);
  }

  // &.top {
  //   background: transparent;
  // }

  &.bg {
    background: var(--bg-black);

    // .header-request--desktop {
    //   border: 1px solid var(--color-black);
    //   svg {
    //     fill: var(--color-black);
    //   }
    //   span {
    //     color: var(--color-black);
    //   }
    // }

    // .header-logo {
    //   svg {
    //     fill: var(--color-black);
    //   }
    // }

    // .header-contacts-phone {
    //   span {
    //     color: var(--color-black);
    //   }
    // }

    // .header-burger {
    //   span {
    //     color: var(--color-black);
    //   }
    // }

    // .header-burger-lines--open {
    //   rect {
    //     fill: var(--color-black);
    //   }
    // }

    &.on {
      .header-burger {
        span {
          color: var(--color-white);
        }
      }
    }
  }

  &.on {
    .header-inner {
      background: transparent;
    }

    .header-left,
    .header-logo-wrap,
    .header-choose-apartment {
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
    }

    .header-menu-wrap {
      opacity: 1;
      visibility: visible;
    }

    .header-burger-lines--open {
      width: 0;
      height: 0;
    }

    .header-burger-lines--close {
      width: 1.25vw;
      height: 1.25vw;

      @include tablet {
        width: 3.125vw;
        height: 3.125vw;
      }

      @include mobile {
        width: 6.66vw;
        height: 6.66vw;
      }
    }
  }

  &-inner {
    position: relative;
    z-index: 10;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--p20) var(--p291);
    transition: all 0.45s ease-in-out;
  }

  &-left {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: var(--g80);
    width: 33.33%;

    @include tablet {
      order: 2;
      width: fit-content;
    }
  }

  &-request-wrap {
    @include tablet {
      order: 2;
    }
  }

  &-request--desktop {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--g20);
    padding: var(--p18) var(--p28);
    border: 1px solid var(--color-white);
    border-radius: 100px;

    @include tablet {
      border-radius: 50%;
      height: 7.8vw;
      width: 7.8vw;
      border: 1px solid var(--color-pantone-2);
      order: 2;
      padding: 0;
    }

    @include mobile {
      display: none;
    }

    span {
      @include tablet {
        display: none;
      }
    }

    svg {
      fill: var(--color-white);
      width: 1.25vw;
      height: 1.25vw;

      @include tablet {
        width: 60%;
        height: 60%;
        fill: none;
        stroke: var(--color-pantone-2);
      }
    }
  }

  &-request--mobile {
    display: none;

    @include mobile {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      height: 11.66vw;
      width: 11.66vw;
      border: 1px solid var(--color-pantone-2);

      svg {
        fill: none;
        width: 60%;
        height: 60%;
        stroke: var(--color-pantone-2);
      }
    }
  }

  &-contacts-phone {
    text-decoration: none;

    @include tablet {
      order: 1;
    }

    @include mobile {
      display: none;
    }
  }

  &-logo-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 33.33%;
    height: 100%;

    @include tablet {
      width: 30%;
      justify-content: flex-start;
    }

    @include mobile {
      width: 56%;
    }
  }

  &-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: var(--logo-w);

    @include tablet {
      order: 1;
      // width: fit-content;
    }

    &__svg--desktop {
      width: 100%;
      height: auto;
      fill: var(--color-white);

      @include tablet {
        display: none;
      }
    }

    &__svg--mobile {
      display: none;

      @include tablet {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }

  &-right {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: var(--g80);
    width: 33.33%;

    @include tablet {
      order: 3;
      width: fit-content;
    }
  }

  &-choose-apartment {
    &--desktop {
      @include tablet {
        display: none;
      }
    }

    &--mobile {
      display: none;

      @include tablet {
        display: flex;
        align-items: center;
        justify-content: center;

        border-radius: 50%;
        height: 7.8vw;
        width: 7.8vw;
        border: 1px solid var(--color-pantone-2);
        background: var(--color-pantone-2);

        svg {
          fill: none;
          width: 60%;
          height: 60%;
        }
      }

      @include mobile {
        height: 11.66vw;
        width: 11.66vw;
      }
    }
  }

  &-burger-wrap {
    width: fit-content;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--g20);
  }

  &-burger {
    display: flex;
    align-items: center;
    gap: 0.625vw;

    @include tablet {
      gap: 1.56vw;
    }
    span {
      transition: all 0.4s ease-in-out;

      @include mobile {
        display: none;
      }
    }
  }

  &-burger-lines {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &-burger-lines--open {
    width: 1.25vw;
    height: 1.25vw;

    transition: all 0.4s ease-in-out;

    @include tablet {
      width: 3.39vw;
      height: 3.39vw;
    }

    @include mobile {
      width: 7.22vw;
      height: 7.22vw;
    }

    rect {
      fill: var(--color-white);
    }
  }

  &-burger-lines--close {
    width: 0;
    height: 0;
    fill: var(--color-white);
    transition: all 0.4s ease-in-out;
  }

  &-menu-wrap {
    position: absolute;
    width: 100%;
    height: 100vh;
    background: var(--color-black);
    top: 0;
    right: 0;
    opacity: 0;
    visibility: hidden;
    transition: all 0.7s ease-in-out;
    display: flex;

    @include tablet {
      width: 100%;
      height: 100vh;
    }

    // @include mobile {
    //   background: rgba($color: #000000, $alpha: 0.6);
    // }
  }

  &-menu-left {
    position: relative;
    width: 50%;

    @include tablet {
      height: 100%;
      width: 100%;
      position: absolute;
      z-index: 0;
    }
  }

  &-menu-deco-top {
    position: absolute;
    top: 0;
    right: 0;
    width: 5vw;
    z-index: 2;

    @include tablet {
      width: 13.28vw;
    }
  }

  &-menu-deco-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 5vw;
    z-index: 2;
    @include tablet {
      width: 13.28vw;
    }
  }

  &-menu-img-wrap {
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: relative;
    }
    svg {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      fill: var(--color-white);
      width: 15vw;

      @include tablet {
        display: none;
      }
    }
  }

  &-menu-img {
    position: relative;

    @include tablet {
      display: none;
    }
    &::before {
      content: '';
      z-index: 1;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(23, 23, 23, 0.5);
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: relative;
    }
  }

  &-menu {
    width: 50%;
    display: flex;
    padding-top: var(--header-h);
    justify-content: center;
    background: var(--color-black);
    position: relative;
    z-index: 1;

    @include tablet {
      width: 100%;
      text-align: center;
      align-content: center;
      background: transparent;
      padding-top: 0;
    }
  }

  &-menu-nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: relative;
    // gap: var(--g20);

    @include tablet {
      align-items: center;
      justify-content: center;
    }

    a {
      text-decoration: none;
      overflow: hidden;
      position: relative;
      display: inline-block;
      text-align: left;
      font-size: 2.81vw;
      line-height: 120%;
      width: 100%;

      @include tablet {
        font-size: 5.46vw;
      }

      @include mobile {
        font-size: 6.11vw;
      }

      span {
        transition: 0.2s linear;
        display: inline-block;
        opacity: 1;
      }
      &:after {
        position: absolute;
        bottom: 3vw;
        content: attr(data-text);
        left: 0;
        white-space: nowrap;
        transition: 0.2s linear;
        opacity: 0;
      }

      &:hover {
        span {
          transform: translateY(3vw);
          opacity: 0;
        }
        &:after {
          transform: translateY(3vw);
          opacity: 1;
        }
      }
    }
  }

  &-menu-wrap {
    &.on {
      .header-deco--1 {
        opacity: 1;
      }
    }
  }
}

@keyframes logo {
  0% {
    transform: translateY(-200%); /* Start offscreen */
  }
  50% {
    transform: translateY(20%); /* Overshoot position */
  }

  100% {
    transform: translateY(0); /* Final position */
  }
}

@keyframes logo-backwards {
  0% {
    transform: translateY(200%); /* Start at the final position */
  }

  50% {
    transform: translateY(-20%); /* Overshoot downwards */
  }
  100% {
    transform: translateY(0); /* Move offscreen */
  }
}
